/*Start stylesheet*/
.body {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: auto;
  max-width: 1440px;
  height: auto;
  background-color: darkgrey;
  overflow-x: hidden;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  margin: auto;
  padding-top: 96px;
  background-color: #ffffff;
}

